import React from "react";

 import profile from "../Data/profile2.png"

 import vivek  from "../Data/Vivek-Tomar-Resume.pdf"
import { Box, Image, Text, Button } from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon, DownloadIcon } from "@chakra-ui/icons";
const About = () => {
  return (
    <Box  id="about" className="about section" paddingTop="100" paddingLeft={{ md: "100" }} paddingRight={{ md: "100" }}  >
      <Text fontSize="3xl" as="b">
        About Me
      </Text>
      <Box
        display={{ base: "block", md: "flex" }}
        justifyContent="space-around"
        marginTop={"20"}
        alignItems="center"
        width="100%"
      >
        <Box display="flex" justifyContent={"center"}>
          <Box borderRadius={"50%"} overflow="hidden" width="fit-content" border={"2px solid black"}>
            <Image src={profile} alt="Profile Pic" width={"250px"} className="home-img" />
          </Box>
        </Box>
        <Box
          width={{ base: "100%", md: "550px" }}
          marginTop={{base:"30", md:"0"}}
          height="fit-content"
        >
          <Text fontSize="lg" as="b" id="user-detail-name">
            Hello, I'm Vivek Singh Tomar
          </Text>
          <Text fontSize="lg" id="user-detail-intro">
          Passionate full stack web developer with expertise in creating dynamic and responsive web applications.
          </Text>
          <Button
              as="a"
              rightIcon={<DownloadIcon />}
              href={vivek}
              download="Vivek-Tomar-Resume.pdf"
              className="nav-link resume"
              id="resume-button-1"
              onClick={() =>
                window.open(
                  "https://drive.google.com/file/d/1iWci0tjpfDDdW8ZHJ0a03ODLgsXk1wAV/view?usp=share_link",
                  "_blank"
                )
              }
            >
              {" "}
              Resume
            </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default About;
